import style from '../../../css/view/layout.module.less'
import React from 'react';
import PropTypes from 'prop-types';
import { DropTarget } from 'react-dnd';
import { Rnd } from 'react-rnd'
import Marks from "./Marks"
import Bus from "../../../util/events"



const boxTarget = {
    //  drag source  monitor.getDropResult() 
    drop: (props,monitor,component) => {

        const hasDroppedOnChild = monitor.didDrop();
        if (hasDroppedOnChild && !props.greedy) {
            return;
        }

        const pos = monitor.getClientOffset();
        const item = monitor.getItem();

        const width = props.layWidth * 0.25;
        const height = props.layHeight *0.25;

        // 
        let posX = pos.x - props.layX - props.touchX;
        let posY = pos.y - props.layY - props.touchY;

        // 
        let x = posX - width/2;
        let y = posY - height/2;

        // 
        if(posX - width/2 < 0)
            x = 0;

        if(posX + width/2 > props.layWidth)
            x = props.layWidth - width;

        if(posY - height/2 < 0)
            y = 0;

        if(posY + height/2 > props.layHeight)
            y = props.layHeight - height;

        let rnds = component.state.rnds;
        let rnd = {
            id: props.name + Math.round(Math.random()*10000) + "_" +item.name+ (rnds.length+1),
            name: props.name + "_" +item.name+ (rnds.length+1),
            key: Math.round(Math.random()*100000),
            type: item.name,
            posX: x,
            posY: y,
            width: width,
            height: height,
            color: 'lightgray',
            click: false,
        }

        if(item.name === 'text')
            rnd.mark = ""
        if(item.name === 'image')
            rnd.mark = ""
        if(item.name === 'time')
            rnd.mark = ""
        if(item.name === 'video')
            rnd.mark = ""
        if(item.name === 'layout')
            rnd.mark = ""

        rnds.push(rnd);

        component.setState({
            rnds: rnds
        })
        return {name: 'Layout'}
    },
    hover(props,monitor,component) {
        //console.log(monitor.getItem())
    }
}

@DropTarget(
    // type  'box'
    //ItemTypes.BOX,
    'box',
    // 
    boxTarget,
    //  connect  monitor 
    // connect  DOM  react-dnd  backend 
    (connect, monitor) => ({
        //  DOM 
        connectDropTarget: connect.dropTarget(),
        // drag source drop target 
        isOver: monitor.isOver(),
        // 
        canDrop: monitor.canDrop(),
        isDrop: monitor.getDropResult(),
        isOverCurrent: monitor.isOver({ shallow: true }),
    })
)
class Layout extends React.Component {

    static propTypes = {
        canDrop: PropTypes.bool.isRequired,
        isOver: PropTypes.bool.isRequired,
        connectDropTarget: PropTypes.func.isRequired,
        isOverCurrent: PropTypes.bool.isRequired,
    }

    constructor(props) {
        super(props)
        this.state = {
            rnds: [],
            // layWidth: this.props.layWidth, //touch px
            // layHeight: this.props.layHeight, //touch px
            // layX: this.props.layX, // touch x
            // layY: this.props.layY, // touch y
            //deltaX: 0, // touch x touchx
            //deltaY: 0, //touch y touchy
        }
    }

    onRndClick(rndId,index,e) {

        e.stopPropagation();
        let rnds = this.state.rnds;
        for(let i=0;i<rnds.length;i++){
            if(i === index)
                rnds[i].click = true;
            else
                rnds[i].click = false;
        }
        this.setState({
            rnds:rnds
        })

        // Menu.js
        Bus.emit('showMenu', rndId);
        // Nature.js,rnd
        Bus.emit('showNature', this.state.rnds[index],index);
        // Touch.js,rnd
        this.props.callback(true);
    }

    UNSAFE_componentWillReceiveProps(props) {
        if (props.parentRndClick) {
            let rnds = this.state.rnds;
            for (let i = 0; i < rnds.length; i++) {
                rnds[i].click = false;

                this.setState({
                    rnds: rnds
                })
            }
        }
    }

    componentDidMount(){
        // Menu.js
        Bus.addListener('handleMenu', (type,rndId) => {
            switch (type) {
                case 'delete':// 
                    let rnds = this.state.rnds;
                    let array = [];
                    for(let i=0;i<rnds.length;i++){
                        if(rnds[i].id !== rndId)
                            array.push(rnds[i]);
                    }
                    this.setState({rnds:array});
                    break;
                case 'upTop': // 
                    break;
                case 'copy': // 
                    break;
                case 'clear': // 
                    if(rndId === -1)
                        this.setState({rnds:[]});
                    break;
                case 'parse': // 
                    break;
                default:
                    break;
            }
        });
    }

    componentWillUnmount(){
        Bus.removeListener("handleMenu",()=>{})

        this.setState = (state,callback) => {
            return
        }
    }

    render() {
        const { canDrop, isOver, connectDropTarget,isOverCurrent } = this.props;
        let backgroundColor = '#333';
        // 
        if (canDrop && isOver && isOverCurrent) {
            backgroundColor = 'black';
        }

        let sy = {backgroundColor: backgroundColor}

        //  connectDropTarget  DOM  drag source 
        // connectDropTarget  DOM  drag source 
        return connectDropTarget && connectDropTarget(
            <div className={
                !false ? style["wch-container"] : [`${style["wch-container"]}`,`${style["active"]}`].join(' ')
            }
                 style={sy}>
                {this.state.rnds.map((rnd,index)=>{
                    return (
                        <Rnd
                            className={
                                !rnd.click ? style["wch-rnd-wrap"] : [`${style["wch-rnd-wrap"]}`,`${style["active"]}`].join(' ')
                            }
                            key={rnd.key}
                            default={{
                                x: rnd.posX,
                                y: rnd.posY,
                                width: rnd.width,
                                height: rnd.height,
                            }}
                            onResize={(event,direction,ele,delta,pos)=>{
                                // 
                                let srnd = this.state.rnds[index];
                                // 
                                let trnd = {...srnd}
                                trnd.width = srnd.width + delta.width;
                                trnd.height = srnd.height + delta.height;
                                trnd.posX = pos.x;
                                trnd.posY = pos.y;
                                // Nature.js
                                Bus.emit('showNature', trnd, index);
                            }}
                            onResizeStop={(event,direction,ele,delta,pos)=>{
                                // 
                                let rnds = this.state.rnds;
                                let srnd = rnds[index];
                                // 
                                let trnd = {...srnd}
                                trnd.width = srnd.width + delta.width;
                                trnd.height = srnd.height + delta.height;
                                trnd.posX = pos.x;
                                trnd.posY = pos.y;
                                rnds[index] = trnd;
                                this.setState({rnds:rnds});
                                // Nature.js
                                Bus.emit('showNature', this.state.rnds[index], index);
                            }}
                            onDrag={(event,data)=>{
                                // 
                                event.stopImmediatePropagation();
                                let srnd = this.state.rnds[index];
                                // 
                                let trnd = {...srnd}
                                trnd.posX = data.lastX-this.state.deltaX;
                                trnd.posY = data.lastY-this.state.deltaY;
                                // Nature.js
                                Bus.emit('showNature', trnd, index);
                            }}
                            onDragStop={(event,data)=>{
                                // 
                                let rnds = this.state.rnds;
                                // 
                                let trnd = {...rnds[index]}
                                trnd.posX = data.x;
                                trnd.posY = data.y;
                                rnds[index] = trnd;
                                this.setState({rnds:rnds});
                                // Nature.js
                                Bus.emit('showNature', trnd, index);
                            }}
                            ref={(c)=>{
                                let rnds = this.state.rnds;
                                for(let i=0;i<rnds.length;i++){
                                    if(rnd.key === rnds[i].key){
                                        rnds[i].ref = c;
                                    }
                                }
                            }}
                            onClick={(e)=>(this.onRndClick(rnd.id,index,e))}
                            onContextMenu={(e)=>{
                                this.onRndClick(rnd.id,index,e);
                            }}
                            bounds={'parent'}>
                            <div className={style["wch-rnd-mark"]}>
                                <Marks name={rnd.type}/>
                            </div>
                        </Rnd>
                    )
                })}
            </div>
        );
    }
}

export default Layout;
