import React from 'react';
import PropTypes from 'prop-types';
import { DragSource } from 'react-dnd';

const boxSource = {
	/**
	 * 
	 * @param {*} props  props
	 */
	beginDrag(props) {
		//  monitor.getItem() 
		return {
			name: props.name,
            rndId: props.rndId,
		}
	},
}

@DragSource(
	// type  'box'
	//ItemTypes.BOX,
	'line',
	// 
	boxSource,
	//  connect  monitor 
	// connect  DOM  react-dnd  backend 
	(connect, monitor) => ({
		//  DOM 
		connectDragSource: connect.dragSource(),
		// 
		isDragging: monitor.isDragging()
	}),
)
class Line extends React.Component {

	static propTypes = {
		name: PropTypes.string.isRequired,
		isDragging: PropTypes.bool.isRequired,
		connectDragSource: PropTypes.func.isRequired
	}

	render() {
		const { name, show, isDragging, connectDragSource } = this.props
		let style;
		if(name === "left"){
            style = {
            	position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '100%',
                width: '2px',
                left: '-15px',
            }
		}

		if(name === "top") {
            style = {
                position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '2px',
                width: '100%',
                top: '-15px'
            }
		}

        if(name === "right"){
            style = {
                position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '100%',
                width: '2px',
                right: '-15px'
            }
        }

        if(name === "buttom") {
            style = {
                position: 'absolute',
                border: '1px solid gray',
                backgroundColor: '#333',
                cursor: 'move',
                height: '2px',
                width: '100%',
                bottom: '-15px'
            }
        }

        const opacity = isDragging ? 0.4 : 1;
		const display = show ? 'block' : 'none';
		return connectDragSource && connectDragSource(
				<div style={{ ...style, opacity, display }}></div>
			);
	}
}

export default Line;