import React from 'react';
import PropTypes from 'prop-types';
import { DragSource } from 'react-dnd';
import Marks from './Marks'


const style = {
	border: 'none',
	//backgroundColor: '#666',
    backgroundColor: '#555',
	color: 'white',
	cursor: 'move',
	height: '50px',
	width: '50px',
	textAlign: 'center',
	fontSize: '25px',
	lineHeight: '50px',
	marginTop: '10px',
	marginLeft: '5px'
}

const boxSource = {
	/**
	 * 
	 * @param {*} props  props
	 */
	beginDrag(props) {
		//  monitor.getItem() 
		let id = props.id;
		if(id === -1)
			id = 0-Math.round(Math.random()*100000);
		return {
            id:	id,
			name: props.name,
			enable: props.enable

		}
	},

	// /**
	//  * 
	//  * @param {*} props  props
	//  * @param {*} monitor DragSourceMonitor 
	//  */
	// endDrag(props, monitor) {
	// 	//  item 
	// 	//const item = monitor.getItem();
	// 	// drop 
	// 	const dropResult = monitor.getDropResult();
    //
	// 	//  drop  alert 
	// 	//if (dropResult) {
	// 		//alert(`You dropped ${item.name} into ${dropResult.name}!`);
	// 	//}
	// },
}

@DragSource(
	// type  'box'
	//ItemTypes.BOX,
	'box',
	// 
	boxSource,
	//  connect  monitor 
	// connect  DOM  react-dnd  backend 
	(connect, monitor) => ({
		//  DOM 
		connectDragSource: connect.dragSource(),
		// 
		isDragging: monitor.isDragging()
	}),
)
class Variety extends React.Component {

	static propTypes = {
		name: PropTypes.string.isRequired,
		isDragging: PropTypes.bool.isRequired,
		connectDragSource: PropTypes.func.isRequired
	}


	render() {
		const { isDragging, connectDragSource } = this.props
		const opacity = isDragging ? 0.4 : 1
		//  connectDragSource  DOM  API
		// connectDragSource  DOM 
		return connectDragSource && connectDragSource(
				<div style={{ ...style, opacity }}>
					<Marks name={this.props.name} />
				</div>
			);
	}
}

export default Variety;