import style from '../../../css/view/place.module.less'
import React from 'react';
import PropTypes from 'prop-types';
import { DropTarget } from 'react-dnd';
import Marks from "./Marks"
import Bus from "../../../util/events"
import host from "../../../util/host"



const boxTarget = {
    //  drag source  monitor.getDropResult() 
    drop: (props,monitor,component) => {
        // Touch.js
        Bus.emit('alignment', monitor.getItem().name,monitor.getItem().rndId,props.rndId);
        return {name: 'Place'}
    }
}

@DropTarget(
    // type  'box'
    //ItemTypes.BOX,
    'line',
    // 
    boxTarget,
    //  connect  monitor 
    // connect  DOM  react-dnd  backend 
    (connect, monitor) => ({
        //  DOM 
        connectDropTarget: connect.dropTarget(),
        // drag source drop target 
        isOver: monitor.isOver(),
        // 
        canDrop: monitor.canDrop(),
        isDrop: monitor.getDropResult(),
        isOverCurrent: monitor.isOver({ shallow: true }),
    })
)
class Place extends React.Component {

    static propTypes = {
        canDrop: PropTypes.bool.isRequired,
        isOver: PropTypes.bool.isRequired,
        connectDropTarget: PropTypes.func.isRequired,
        isOverCurrent: PropTypes.bool.isRequired,
    }

    constructor(props) {
        super(props)
        this.state = {
            imgUrl : "",
        }
    }

    onRndClick(rndId,index,e) {

        e.stopPropagation();
    }

    UNSAFE_componentWillReceiveProps(props) {

    }

    componentWillUnmount() {
        this.setState = ()=>false;
    }

    componentDidMount() {
        const { rndId,enable } = this.props;
        let handle = this;
        if(rndId >= 0 && enable+"" == "true") {
            let timerId = setInterval(()=>{
                handle.setState({
                    imgUrl:"http://"+host()+"/snap/snap"+ rndId +".jpg?rnd="+Math.random()
                })
            },500);
        }

    }


    render() {
        const { canDrop, isOver, connectDropTarget,isOverCurrent } = this.props;
        let backgroundColor = 'black';
        // 
        if (canDrop && isOver && isOverCurrent) {
            backgroundColor = '#333';
        }

        //console.log(canDrop,isOver,isOverCurrent)

        let sy = {backgroundColor: backgroundColor}

        let content;
        if(this.props.enable + "" === "true"){
            content = (
                <div style={{position:"absolute",width:"100%",height:"100%"}}>
                    <img style={{width:"100%",height:"100%"}} src={this.state.imgUrl}/>
                </div>
            )
        }
        return connectDropTarget && connectDropTarget(
            <div className={
                !this.props.click ? style["wch-container"] : [`${style["wch-container"]}`,`${style["active"]}`].join(' ')
            }
                 style={sy}>
                {content}
                <div style={{width:"100%",height:"100%",position:"absolute"}}></div>
                <div className={
                    !this.props.click ? style["wch-rnd-mark"] : [`${style["wch-rnd-mark"]}`,`${style["active"]}`].join(' ')
                }>
                    <Marks name={this.props.name}/>
                </div>
            </div>
        );
    }
}

export default Place;
